import { useState } from "react";

import {
  Box,
  NavLink,
  Text,
  Flex,
  Tooltip,
  Button,
  Group,
  rem,
} from "@mantine/core";
import { useLiveQuery } from "dexie-react-hooks";

import { db } from "../storage/db";
import { SUN_WYRMMS } from "../metas/Metas";
import { IconTrash } from "@tabler/icons-react";

const Navigation = ({
  setAttackPoints,
  setUtilityPoints,
  setTankPoints,
}: {
  setAttackPoints: Function;
  setUtilityPoints: Function;
  setTankPoints: Function;
}) => {
  const [active, setActive] = useState("");
  const setups = useLiveQuery(() => db.setups.toArray());

  const navSection = (
    <Box>
      <NavLink label="Metas" childrenOffset={0}>
        <NavLink
          active={"sunwrymms" === active}
          label={
            <Text size="sm" ml="md">
              Sun Wrymms
            </Text>
          }
          onClick={() => {
            setActive("sunwrymms");
            setAttackPoints(SUN_WYRMMS.attack);
            setUtilityPoints(SUN_WYRMMS.utility);
            setTankPoints(SUN_WYRMMS.tank);
          }}
        />
      </NavLink>
      <NavLink key={setups?.length} defaultOpened={true} label="My Bestiaries" childrenOffset={0}>
        {setups?.map((setup) => (
          <NavLink
            key={setup.id}
            active={`${setup.id}` === active}
            label={
              <Group grow>
                <Text size="sm" ml="md">
                  {setup.name}
                </Text>
                <Flex
                  gap="md"
                  justify="flex-end"
                  align="center"
                  direction="row"
                  wrap="wrap"
                >
                  <Tooltip label="Delete bestiary" position="right" offset={5}>
                    <Button
                      size="compact-xs"
                      variant="light"
                      color="red"
                      onClick={(event: React.MouseEvent<HTMLButtonElement, MouseEvent>) => {
                        event.stopPropagation();
                        db.setups.where("id").equals(setup.id!).delete()
                      }}
                    >
                      <IconTrash style={{ width: rem(16), height: rem(16) }} />
                    </Button>
                  </Tooltip>
                </Flex>
              </Group>
            }
            onClick={() => {
              setActive(`${setup.id}`);
              setAttackPoints(setup.attack);
              setUtilityPoints(setup.utility);
              setTankPoints(setup.tank);
            }}
          />
        ))}
      </NavLink>
    </Box>
  );

  return navSection;
};

export default Navigation;
